import { motion } from "motion/react"
import { assets } from "@/assets/assets"
import Image from "next/image"

function Header() {
    return (
        <div className="w-11/12 max-w-3xl text-center mx-auto h-screen flex flex-col items-center justify-center gap-4">
            <motion.div
              initial={{scale:0 }}
              whileInView={{ scale:1 }}
              transition={{duration:0.8,type:'spring',stiffness:100}}
              >
                <Image src={assets.profile_img} alt="" className="rounded-full w-32"/>
                
            </motion.div>
            <motion.h3 className="flex items-end gap-2 text-xl md:text-2xl mb-3"
            
            initial={{y:-20 ,opacity:0}}
              whileInView={{ y:0,opacity:1 }}
              transition={{duration:0.8,delay:0.3}}>
                    Hi! I am Amit Pant <Image src={assets.hand_icon} className="w-6" alt=""/> </motion.h3>

            <motion.h1 className="text-3xl sm:text-6xl lg:text-[66px] "
             initial={{y:-30 ,opacity:0}}
             whileInView={{ y:0,opacity:1 }}
             transition={{duration:0.6,delay:0.3}}>
                Front End Developer</motion.h1>
            <motion.p className="max-w-2xl mx-auto"
                         initial={{opacity:0}}
                         whileInView={{ opacity:1 }}
                         transition={{duration:0.6,delay:0.7}}>
                    I am front end developer from Haldwani, with 6 months of experience in Food Dude Inc.
            </motion.p>
            <div className="flex flex-col sm:flex-row items-cemter gap-4 mt-4">
                <motion.a 
                    initial={{y:30 ,opacity:0}}
                    whileInView={{ y:0,opacity:1 }}
                    transition={{duration:0.6,delay:1}}
                href="contact" className="px-10 py-3 border border-white rounded-full bg-black text-white flex items-center gap-2">contact me <Image src={assets.right_arrow_white} alt="" className="w-4"/></motion.a>
                <motion.a href="/sample-resume.pdf" download className="px-10 py-3 border rounded-full border-gray-500 flex"
                             initial={{y:30 ,opacity:0}}
                             whileInView={{ y:0,opacity:1 }}
                             transition={{duration:0.6,delay:1.2}}>My resume <Image src={assets.download_icon} alt="" className="w-4"/></motion.a>
            </div>
        
        </div>
    )
}

export default Header
